.modal {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.2s linear;
  pointer-events: none;
  z-index: 100;
}
.modal.active {
  opacity: 1;
  pointer-events: all;
}
.modal__content {
  /* padding: 30px 50px; */
  /* border-radius: 12px; */
  transform: scale(0.9);
  transition: all 0.3s linear;
  /* width: 30%; */
  height: auto;
  background-color: #fff;
}
.modal__content.active {
  transform: scale(1);
}
